		</cu-custom>
		<!-- 头部 -->
		<view class="my-header">
			<view class="my-header_container flex">
				<image src="/static/logo.png" class="user-photo" mode="aspectFit"></image>
				<view class="my-header-right padding-lr">
					<view class="nickname text-black text-bold">我是昵称</view>
					<view>ID：425125879651<text class="margin-left-sm text-sm text-grey">点击复制</text></view>
