					focusNum: 3,
					integral: 1001
				},
				publish: {
					goodsNum: 300,
					noteNum: 100
				}
			}
		},
		methods: {
			onGoPage(item) {
				if (item['link']) {
					uni.navigateTo({
						url: item.link
					})
				}
			}
		}
	}
</script>

